.grid-item {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  min-height: 240px;
  @apply shadow-md
}

.grid-item:nth-child(1) {
  background: #009687f5;
}

.grid-item:nth-child(2) {
  background: #dc7657f5;
}

.grid-item:nth-child(3) {
  background: #4bb3c1fa;
}

.grid-item:nth-child(4) {
  background: #fac069f9;
}

.grid-item:nth-child(5) {
  background: #67a866f9;
}

.grid-item:nth-child(6) {
  background: #b94169fa;
}

.grid-item:nth-child(7) {
  background: #7f62b3fa;
}

.grid-item:nth-child(8) {
  background: #9fc376f9;
}

.grid-item:nth-child(9) {
  background: #677bcbfa;
}

@media (min-width: 550px) {
  /* #grid div {
    width: calc(100% / 3);
  } */
}

.grid-item h3 {
  font-size: 2em;
  color: #eee;
}

.grid-item img {
  max-width: 100%;
  padding-bottom: 1em;
}

.grid-item p {
  background: rgba(000, 000, 000, 0.3);
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: .75em;
  margin: 0;
  box-sizing: border-box;
  font-size: 1.2em;
  font-weight: 600;
  color: #fff;
}

/* Form Styles */
.form-container {
  background: #eee;
  padding: 1.3em;
  text-align: left;
  max-width: 400px;
  margin: 0 auto;
}

form p {
  margin-bottom: 0.2em;
  font-size: 1.3em;
  font-weight: 600;
  font-family: 'Oswald', sans-serif;

}

form p:not(:first-of-type) {
  margin-top: .75em;
}

input {
  padding: 0.7em;
  background: #fff;
  border: 0;
}

.form-field__short {
  width: 27%;
  margin-right: 3%;
  box-sizing: border-box;
}

.form-field__full {
  width: 100%;
  box-sizing: border-box;
}

select {
  display: block;
}

#submit-button {
  display: inline-block;
  background: #ccc;
  padding: 0.8em;
  margin: 1.2em auto;
  transition: ease 0.3s all;
}

#submit-button:hover {
  background: #777;
  color: #fff;
  cursor: pointer;
}

/* Footer */
.fine-print {
  display: block;
  font-size: 0.7em;
}
